body {
  background:
    linear-gradient(90deg, #fff 7px, transparent 1%) 50%,
    linear-gradient(#fff 7px, transparent 1%) 50%,
    #000;
  background-attachment: fixed;
  background-size: 8px 8px;
}
a {
  color: #000;
  text-decoration: underline;
}
hr {
  border-top: 0.15em solid #000;
}
.heading,
h1 {
  font-size: 1em;
}
.heading,
h1,
h2 {
  font-family: Chicago;
}
h2 {
  font-size: 2em;
}
.standard-button {
  display: block;
  min-height: 20px;
  min-width: 59px;
}
.standard-dialog {
  background-color: #fff;
  border: 2px solid;
  box-shadow: 2px 2px 0 0 #000;
  padding: 10px;
}
.alert-box,
.modal-dialog {
  background: #fff;
  padding: 13px;
  width: auto;
}
.alert-contents,
.modal-contents {
  padding: 10px;
}
.modeless-dialog {
  font-size: 18px;
  height: 100%;
  letter-spacing: -0.025em;
  padding: 1.5rem 0.5rem 0.5rem;
}
.inner-border {
  border-color: currentcolor;
  border-style: solid;
  border-width: 3.5px 5px;
}
.outer-border {
  border: 2px solid;
  padding: 3px;
}
.title-bar {
  align-items: center;
  background: linear-gradient(#000 50%, transparent 0);
  background-clip: content-box;
  background-size: 6.6666666667% 13.3333333333%;
  display: flex;
  flex: none;
  height: 20px;
  margin: 0.1rem 0;
  padding: 0.2rem 0.1rem;
}
.title-bar .title {
  background: #fff;
  cursor: default;
  font-family: Chicago_12;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.1;
  margin: 0 auto;
  padding: 0 0.5em;
  text-align: center;
}
.inactive-title-bar {
  align-items: center;
  background-clip: content-box;
  background-size: 6.6666666667% 13.3333333333%;
  display: flex;
  flex: none;
  height: 20px;
  margin: 0.1rem 0;
  padding: 0.2rem 0.1rem;
}
.inactive-title-bar .title {
  color: #a5a5a5;
  cursor: default;
  font-family: Chicago_12;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.1;
  margin: 0 auto;
  padding: 0 0.5em;
  text-align: center;
}
.title-bar button {
  background-color: #fff;
  border: 4px solid #000;
  cursor: pointer;
  display: block;
  height: 40px;
  margin: 0 0.2rem;
  position: relative;
  transform: scale(0.5);
  width: 40px;
}
.title-bar button span {
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
}
.title-bar button.close:after,
.title-bar button.close:before {
  bottom: 0;
  content: '';
  left: 0;
  opacity: 0;
  position: absolute;
  right: 0;
  top: 0;
}
.title-bar button.close:before {
  background:
    linear-gradient(#000, #000) 0,
    linear-gradient(#000, #000) 100%,
    linear-gradient(#000, #000) top,
    linear-gradient(#000, #000) bottom;
  background-repeat: no-repeat;
  background-size:
    30% 4px,
    30% 4px,
    4px 30%,
    4px 30%;
}
.title-bar button.close:after {
  background:
    linear-gradient(#000, #000) 0,
    linear-gradient(#000, #000) 100%,
    linear-gradient(#000, #000) top,
    linear-gradient(#000, #000) bottom;
  background-repeat: no-repeat;
  background-size:
    22.5% 3.6363636364px,
    22.5% 3.6363636364px,
    3.6363636364px 22.5%,
    3.6363636364px 22.5%;
  transform: rotate(45deg) scale(1.1);
}
.title-bar button.close:active:after,
.title-bar button.close:active:before {
  opacity: 1;
}
.title-bar button.resize {
  background:
    linear-gradient(#000, #000) left 58%,
    linear-gradient(180deg, #000 0, #000) 58% top;
  background-color: #fff;
  background-repeat: no-repeat;
  background-size:
    60% 4px,
    4px 60%;
}
.title-bar button.resize:active {
  background: #fff;
}
.title-bar button.hidden {
  visibility: hidden;
}
.window {
  background-color: #fff;
  border: 2px solid #000;
  flex-direction: column;
  font-family: Inconsolata, Menlo, Chicago, Geneva;
  margin: 1rem;
  min-width: 320px;
  overflow: hidden;
}
.window-pane {
  font-size: 18px;
  height: 100%;
  letter-spacing: -0.025em;
  overflow-y: scroll;
  padding: 1rem 2rem;
}
.window-pane::-webkit-scrollbar {
  background-color: #fff;
  width: 18px;
}
.window-pane::-webkit-scrollbar-track {
  background:
    linear-gradient(45deg, #000 25%, transparent 0, transparent 75%, #000 0, #000),
    linear-gradient(45deg, #000 25%, transparent 0, transparent 75%, #000 0, #000);
  background-color: #fff;
  background-position:
    0 0,
    2px 2px;
  background-size: 4px 4px;
  border-left: 4px solid #000;
  width: 10px;
}
.window-pane::-webkit-scrollbar-thumb {
  background-color: #fff;
  border: 2px solid #000;
  border-right: none;
  box-sizing: content-box;
  width: 18px;
}
.separator {
  border-width: 0.1rem 0;
  border-top: 0.1rem solid #000;
}
.details-bar,
.separator {
  display: flex;
  flex: none;
  flex-direction: row;
  font-size: 1rem;
  justify-content: space-between;
}
.details-bar {
  border: 0.1rem solid #000;
  border-width: 0.1rem 0;
  font-weight: 100;
  padding: 0.4rem 0.8rem;
}
.btn,
.btn-default {
  background: #fff;
  border-image: url(button.svg) 30 stretch;
  border-style: solid;
  border-width: 5.5px;
  color: #000;
  cursor: pointer;
  font-family: Chicago_12;
  font-size: 18px;
  min-height: 20px;
  min-width: 59px;
  padding: 0 20px;
  text-align: center;
  text-decoration: none;
}
.btn:active {
  background: #000;
  border-radius: 6px;
  color: #fff;
  font-family: Chicago_12;
}
.btn:focus-visible {
  outline: 2px dotted #000;
  outline-offset: 2px;
}
.btn-default:focus-visible {
  outline: 2px dotted #000;
  outline-offset: 2px;
}
.btn:disabled {
  background: #fff;
  border-image: url(button.svg) 30 stretch;
  border-style: solid;
  border-width: 5.5px;
  color: #b6b7b8;
  font-family: Chicago_12;
  font-size: 18px;
  min-height: 20px;
  min-width: 59px;
  text-align: center;
  text-decoration: none;
}
.btn-default {
  border-image: url(button-default.svg) 60 stretch;
  border-width: 0.5em;
}
.btn-default:active {
  border-radius: 12px;
}
